<template>
  <div class="vip-dialog">
    <Modal v-model="openConfigDialog" :footer-hide="true">
      <div class="vip-wrapper">
        <h2>My Membership Card</h2>
        <RadioGroup v-model="passportId" class="scroll-warpper" @on-change="getChoseValue">
          <div v-for="(item, index) in tokenList" :key="index" class="vipCard-warpper">
            <div class="flex items-center justify-between mb-4">
              <Radio :label="item.token" />
              <h2 class="card-title">
                {{ item.name }}
              </h2>
            </div>
            <p>
              passport ID
              <span>{{ item.token }}</span>
            </p>
          </div>
        </RadioGroup>

        <div class="btn-wrapper">
          <button @click="purchaseWithVip">
            Confirm
          </button>
          <button @click="openConfigDialog = false">
            Cancel
          </button>
        </div>
      </div>
    </Modal>
  </div>
</template>

<script>
import { fetchGet } from '@/common/utils/request.js';
import { purchase } from '@/contactLogic/GenArt/index.js';
import event from '@/common/utils/event.js';
import { mapState } from 'vuex';
import {deepFlatten} from '@/common/utils/utils.js';
export default {
  data() {
    return {
      openConfigDialog: false,
      passportId: '',
      data: {},
      tokenList: []
    };
  },
  computed: {
    ...mapState(['ethAddress', 'ethersprovider', 'ethChainID']),
  },
  methods: {
    async open(data) {
      await this.getVipCards();
      this.data = data;
      this.openConfigDialog = true;
    },
    getChoseValue(val) {
      console.log(val);
      this.passportId = val;
    },
    async purchaseWithVip() {
      if (!this.passportId) {
        console.log('未选择');
        return false;
      }
      console.log(this.passportId);
      try {
        const { library, account, chainID, projectId, pricePerTokenInWei, isPassport, passportId } = {
          library: this.ethersprovider,
          account: this.ethAddress,
          chainID: this.ethChainID,
          projectId: this.data.project_id,
          pricePerTokenInWei: this.data.token_info?.PricePerTokenInWei,
          isPassport: true,
          passportId: this.passportId,
        };
        const res = await purchase({
          library,
          account,
          chainID,
          projectId,
          pricePerTokenInWei,
          isPassport,
          passportId,
        });
        console.log({ res });
        this.$Notice.success({
          title: 'Transaction sent successfully',
          duration: 10,
        });
        event.$emit('sendtx', [
          res,
          {
            okinfo: `Successful purchase`,
            failinfo: `Purchase failed`,
          },
        ]);
        // this.openPurchase = false;
      } catch (err) {
        console.log(err);
        this.$Notice.error({
          title: 'Failed to send transaction',
          desc: err?.error?.message,
          duration: 10,
        });
      }
    },
    async getVipCards() {
      try {
        const res = await fetchGet(`/api/vipProject/${this.ethAddress}`);
        // console.log(res);
        const cardList = res.data;
        // console.log(this.cardList);
        const tempTokenList = cardList.map((item) => {
          const tokens = item.tokens.map(tokenItem => {
            return {
              name: item.project.project_name,
              token:tokenItem.token_id
            };
          });
          return tokens;
        });
        this.tokenList = deepFlatten(tempTokenList);
        // console.log({ tokenList });
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

